// створення контейнеру для галереї зображень
const galleryContainer = document.querySelector('.gallery');

// розмітка елементів галереї
const createProductCardTemplate = images
  .map(product => {
    return `
  <li class="gallery-item">
  <a class="gallery-link" href="${product.original}">
    <img
      class="gallery-image"
      src="${product.preview}"
      alt="${product.description}"
      width="360"
      height="200"
    />
  </a>
</li>
  `;
  })
  .join('');

// додавання розмітки до списку ul.gallery
galleryContainer.insertAdjacentHTML('beforeend', createProductCardTemplate);

new SimpleLightbox('.gallery a', {
  captions: true,
  captionsData: 'alt',
  captionDelay: 250,
});
